import { VNode, CreateElement } from 'vue'
import { Component, Prop, Model, Watch, Mixins } from 'vue-property-decorator'
import omit from 'lodash/omit'
import { Col } from 'element-ui'
import MethodsMixins from './mixins/methods'
import './custom/fragment'
import { Fragment } from 'vue-frag'
import { cloneDeep, isFunction, isString, isArray } from 'lodash'
import objectPath from './utils/object-path'
import { deepQuery, isDefined } from './utils'
// 
import './styles/index.scss'

// vnode
import { Vnodes, DetailVnodes, SuperCustom } from './vnode'

interface IVnodes {
  [key: string]: any
  [index: number]: any
}
interface IModel {
  [key: string]: any
  [index: number]: any
}

@Component({
  name: 'ElFormPlus',
  components: { Fragment },
})
export default class ElFormPlus extends Mixins(MethodsMixins) {
  // formData
  @Model('change', { type: Object }) readonly modelData!: any

  //  https://element.eleme.cn/#/zh-CN/component/layout#row-attributes
  @Prop({ type: Object, default: () => ({}) }) readonly layout?: any

  //  https://element.eleme.cn/#/zh-CN/component/form#form-attributes
  @Prop({ type: Object, default: () => ({}) }) readonly config!: any

  // 
  @Prop({ type: Array, default: () => [] }) readonly options!: any[]
  // 
  @Prop({ type: Object, default: () => ({}) }) readonly unifyOptions!: any

  public model: IModel = {}

  public data: any[] = []

  public listeners: any = null

  created() {
    // 
    // Watch immediatetruecreated
    // modelDataoptions
    this.bindData(this.modelData)

    // render
    this.listeners = this.$listeners
  }

  // this.model.x = xx 
  // this.model = { x: xx }  model
  @Watch('modelData', { deep: true })
  modelDataChange() {
    this.bindData(this.modelData)
    this.$emit('change', this.model)
  }

  get cacheModelData() {
    return JSON.parse(JSON.stringify(this.modelData))
  }

  objDiff(a: IModel, b: IModel) {
    const c: string[] = []
    Object.keys(b).map(key => {
      // a
      // akey value
      if (!isDefined(a[key]) || a[key] !== b[key]) {
        // c[key] = b[key]
        c.push(key)
      }
    })
    return c
  }

  @Watch('cacheModelData', { deep: true })
  cacheModelDataChange(value: IModel, oldValue: IModel) {
    const changedModelKey = this.objDiff(oldValue, value)
    changedModelKey.forEach(o => {
      const option = deepQuery(this.options, o, 'field')
      if (option) {
        const { on = {} } = option as any
        const { modelChange } = on
        modelChange && isFunction(modelChange) && modelChange(value[o])
      }
    })
  }

  // options
  // 
  @Watch('options', { immediate: true, deep: true })
  public setData() {
    const options = this.options
    this.data = options
  }

  // options
  // optionfield model
  @Watch('data', { immediate: true, deep: true })
  public dataChange(val: any, oldVal: any) {
    const options = val
    this.buildModel(options)
    // model
    this.$emit('change', this.model)
    // this.exportInstance()
  }

  // 
  public bindData(data: any) {
    for (const o in data) {
      if (Object.prototype.toString.call(data[o]) === '[object Object]') {
        this.bindData(data[o])
      }
      this.$set(this.model, o, data[o])
    }
  }

  // model
  public buildModel(data: any) {
    for (const o of data) {
      const result = this.isFieldExist(o)
      if (!result) {
        continue
      }
      const { field, value, more } = o
      if (field) {
        const modelValue = this.model[field]
        const finalValue = isDefined(modelValue) ? modelValue : value
        this.$set(this.model, field, finalValue)
      }
      if (more && isArray(more)) {
        this.buildModel(more)
      }
    }
  }

  // 
  //  attrs field
  public isFieldExist(attrs: any): boolean {
    // field
    if (attrs.noField) return true
    const isExist = objectPath.has(attrs, 'field')
    if (!isExist) {
      console.error('field')
      return false
    }
    return true
  }

  // type 
  public renderWhatComponent(type: any) {
    const vnodes: IVnodes = Vnodes
    return vnodes[type]
  }

  public renderWhatDetailComponent(type: any) {
    const vnodes: IVnodes = DetailVnodes
    return vnodes[type]
  }

  // containerEl
  public renderContainerEl(c: any) {
    if (c) {
      if (isFunction(c)) {
        // eslint-disable-next-line no-useless-call
        return c.call(null, this)
      }
      if (isString(c)) {
        return c
      }
    }
    return 'FFragment'
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  render(h: CreateElement): VNode {
    const model = this.model

    const {
      row: globalRowConfig = { gutter: 20 },
      col: globalColConfig = { span: 12 },
    } = this.layout || {}

    const {
      container: globalContainer,
      full = false,
      detailPattern = 'form',
    } = this.config
    // 
    const renderSingleForm = (singleFormAttrs: any) => {
      const { attrs: unifyAttrs = {} } = this.unifyOptions
      const { placeholder, disabled } = singleFormAttrs
      const {
        type = '',
        attrs = {},
        layout,
        col,
        field = '',
        detail = {},
        container,
        on = {},
        scopedSlots = {},
      } = singleFormAttrs

      // attrs
      const shortcutAttrs = { placeholder, disabled }

      // 
      const customScopedSlots: { [key: string]: any } = {}
      for (const key in scopedSlots) {
        if (isString(scopedSlots[key])) {
          customScopedSlots[key] = this.$scopedSlots[scopedSlots[key]]
        } else {
          customScopedSlots[key] = scopedSlots[key]
        }
      }

      // input
      const customInputEvent = (val: any) => {
        if (field) this.$set(model, field, val)

        // input
        const { input } = on
        if (!input) return
        if (isFunction(input)) {
          // eslint-disable-next-line no-useless-call
          input.call(null, val)
        } else {
          console.error(`field='${field}'input`)
        }
      }

      // 
      const value = model[field]

      // input
      const extraEvents = omit(on, ['input'])

      // el-col
      // el-form-item
      const ColEl: any = layout ? Col : 'FFragment'

      // container
      // 
      const ContainerEl = this.renderContainerEl(container)

      //  SuperComponent
      const TrueComponent: any = this.renderWhatComponent(type)

      const DetailComponent: any = this.renderWhatDetailComponent(type)
      return (
        <ColEl {...{ props: { ...globalColConfig, ...col } }}>
          <ContainerEl>
            {detailPattern === 'desc' ? (
              <DetailComponent
                {...{
                  scopedSlots: customScopedSlots,
                  attrs: {
                    ...unifyAttrs,
                    ...shortcutAttrs,
                    ...attrs,
                    ...{ detail },
                  },
                  on: {
                    ...extraEvents,
                  },
                }}
                class={attrs.extraClass}
                value={value}
              ></DetailComponent>
            ) : (
              <TrueComponent
                {...{
                  scopedSlots: customScopedSlots,
                  attrs: { ...unifyAttrs, ...shortcutAttrs, ...attrs },
                  on: {
                    ...extraEvents,
                    input: customInputEvent,
                  },
                }}
                class={attrs.extraClass}
                value={value}
              />
            )}
          </ContainerEl>
        </ColEl>
      )
    }

    //  el-form-item
    const renderElFormItem = (o: any) => {
      const { config: unifyConfig = {} } = this.unifyOptions
      // 
      const singleFormAttrs = omit(o, ['hidden', 'config', 'more'])

      const { label, field = '', config = {}, more = [], layout } = o
      const mergeConfig = { ...unifyConfig, ...config }
      const {
        col = globalColConfig,
        container,
        cancelrule = false,
      } = mergeConfig
      // config
      const shortcutConfig = { label }

      const isHasField = this.isFieldExist(singleFormAttrs)

      // el-form-item
      const ColEl = this.layout ? 'el-col' : 'FFragment'

      // el-form-item
      const RowEl = layout ? 'el-row' : 'FFragment'

      // container
      const ContainerEl = this.renderContainerEl(container)

      // 
      const moreForm = () => {
        return more.map((o: object) => {
          // layoutmorelayout
          const props: any = o || {}
          props.layout = layout
          return renderSingleForm(props)
        })
      }

      return (
        <ColEl {...{ props: { ...globalColConfig, ...col } }}>
          <ContainerEl>
            <el-form-item
              class={mergeConfig.class}
              style={mergeConfig.style}
              {...{
                props: {
                  prop: field,
                  ...shortcutConfig,
                  ...omit(mergeConfig, cancelrule ? ['rules'] : []),
                },
              }}
            >
              <RowEl {...{ props: { ...globalRowConfig, ...layout } }}>
                {isHasField &&
                  [renderSingleForm(singleFormAttrs)].concat(moreForm())}
              </RowEl>
            </el-form-item>
          </ContainerEl>
        </ColEl>
      )
    }

    const renderSuperCustom = (options: any) => {
      const { scopedSlots, col = { span: 24 } } = options
      const customScopedSlots = isString(scopedSlots)
        ? { custom: this.$scopedSlots[scopedSlots] }
        : { custom: scopedSlots }
      const ColEl = this.layout ? 'el-col' : 'FFragment'
      return (
        <ColEl {...{ props: { ...globalColConfig, ...col } }}>
          <SuperCustom
            {...{
              scopedSlots: customScopedSlots,
            }}
          ></SuperCustom>
        </ColEl>
      )
    }

    const renderItem = () => {
      const options = this.data

      // SuperCustomel-form
      // el-form-item()el-row()
      return options
        .filter((o: any) =>
          isFunction(o.hidden) ? !o.hidden(model) : !o.hidden
        )
        .map((o: any) => {
          if (o.type === 'SuperCustom') {
            return renderSuperCustom(o)
          }
          return renderElFormItem(o)
        })
    }

    // el-row
    const RowEl = this.layout ? 'el-row' : 'FFragment'
    //  
    const GlobalContainer = this.renderContainerEl(globalContainer)
    // el-form
    return (
      <el-form
        ref="ElForm"
        class={[
          full ? 'el-form_full' : '',
          detailPattern === 'desc' ? 'el-form_detail-mode' : '',
        ]}
        {...{
          props: {
            ...this.config,
            model: model,
          },
          on: this.listeners,
        }}
      >
        <RowEl {...{ props: { ...globalRowConfig } }}>
          <GlobalContainer>{renderItem()}</GlobalContainer>
        </RowEl>
      </el-form>
    )
  }
}
